.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header .nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header .nav__list .nav__item:not(:last-child) {
  margin-right: 20px;
}

.header .nav__list .nav__item .nav__link {
  text-transform: uppercase;
  color: #060b35;
  padding: 23px 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  transition: all .3s;
  display: block;
}

.header .nav__list .nav__item .nav__link:hover, .header .nav__list .nav__item .is-active {
  color: #00acdc;
}

.header .nav__list .nav__item .is-active {
  position: relative;
}

.header .nav__list .nav__item .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  font-family: Roboto, sans-serif;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover__product-name {
  color: #34568b;
}

.card__container {
  margin-left: 16px;
  margin-right: 16px;
}

.card__product-photo {
  justify-content: center;
  display: flex;
}

.card__buy-button {
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border-style: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-bottom: 40px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__product-name {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__review .reviews {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

h1, h2, h3, h4, div, body, p, ul, ol {
  margin: 0;
}

ul, ol {
  padding: 0;
  list-style: none;
}

li, a {
  text-decoration: none;
}

li {
  padding: 0;
}
